height: 10vh;
display: flex;
justify-content: flex-end;

}
 .setting-2{
   width: 2.2rem;
   height: 3rem;
   color: orange;
   font-size: 2rem;
   /* border: 1px solid rebeccapurple; */
 position: absolute;   
   }


 .setting-2 i{
  animation:rotate 2s infinite ;
 }
 @keyframes rotate{
   10%{transform: rotate(0deg)}
     100%{transform: rotate(360deg)}
 }
