background:black;
/* background-image: url("./line.gif"); */
background-size: 100% 100%;
display: flex;
align-items: center;
justify-content: space-between;
}

.setting{
    width: 17rem;
    height: 10vh;
    /* border: 1px solid white; */
display: flex;
justify-content: space-between;
align-items: center;
}
.pic-1{
    width: 3rem;
