    background-image: url(./react.gif);
    background-size: 100% 100%;
    border-radius: 50px;
    /* border: 1px solid white; */
}
.pic-2{
    width: 3rem;
    height: 8vh;
    background-image: url(./vu-2.gif);
    background-size: 100% 100%;
    border-radius: 50px;
    /* border: 1px solid white; */
    z-index: 2;
}
.pic-3{
    width: 3rem;
