.logo{
    width:7rem;
    height: 10vh;
    /* border: 1px solid white; */
    /* background-image: url(./eyes.gif); */
    background-size: 100% 100%;
}
.show-nav{
    width: 2.9rem;
    height: 2.9rem;
    
    font-size: 1.2rem;
    background-image: url("./show.gif");
    background-size: 100% 100%;
    padding-top: 12px;
  text-align: center;
    box-sizing: border-box;
    color: red;
    /* border: 2px solid red; */

}   

.i-2{
    display: none;
    font-size: 1.2rem;
    animation: infinite 10s color;
color: red;
}




/* Nav Bar */
.nav-bar{
    width: 15rem;
    height: 90vh;
background-image: url('./nav.gif');
z-index: 10;
 background-size: 100% 100%;
border-radius: 0px 20px 20px 0px;
display: flex;
flex-direction: column;
justify-content: space-between;
align-items: center;
position: absolute;
left: -17rem;
}

.pic{
    width: 14.5rem;
    height: 13rem;
    /* border-radius: 100%; */
    /* border: 1px solid pink; */
    opacity: 0.1;
}

.pic img{
    width: 100%;
    height: 100%;
    border-radius: 0px 20px 0px 0px;
}
.conent{
    width: 9rem;
    height: 14rem;
 /* border: purple solid 1px; */
color: white;
text-align: center;
display: flex;

}
.icons{
    width: 3rem;
    height: 9rem;
    color: white;
    /* border:  1px solid red; */
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.conent .ul ul{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    font-family: algerian;
    /* background: chartreuse; */
    width:4rem ;
height: 9rem;
}



